<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                v-model="barcodeNumber"
                placeholder="请输入条码"
                show-action
                shape="round"
                clear
                @search="dealBarcodeNumber"
                style="margin-right:1rem;margin-left:0.1rem"
                right-icon
                @keyup.enter="dealBarcodeNumber"
                >
                <div slot="action" disabled="loading" @click="dealBarcodeNumber">搜索</div>
                <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix='icon' name="saomiao" /></div>
            </van-search>
        </div>
        <div class="form-item">
            <h2 class="form-header">条码明细</h2>
            <van-collapse v-model="activeName" accordion>
                <van-collapse-item :title="item.materialBarcode" :name="item.id" v-for="item in materialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.mNumber"
                                label="物料编码："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.mName"
                                label="物料名称："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.mSum"
                                label="数量："
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.unitName"
                                label="单位："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.warehouse"
                                label="仓库："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.position"
                                label="仓位："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="24">
                            <van-field
                                v-model="item.batch"
                                label="批号："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div>
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack">返回</van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit">拆包</van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            barcodeNumber:'',
            list: [],
            loading: false,
            finished: false,
            activeName:'',
            materialList:[],
            sum:''
        };
  },
  methods: {
        dealBarcodeNumber(){
            Toast.loading({
                duration:0,
                message: '获取数据...',
                forbidClick: true
            });
            this.$api.get("/searchBarcodeNumber",{BarcodeNumber:this.barcodeNumber},response=>{
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        var json=JSON.parse(response.data.msg);
                        this.$set(json,'id',this.materialList.length+1);
                        this.$set(json,'materialBarcode',this.barcodeNumber);
                        this.materialList=[];
                        this.materialList.push(json);
                        this.sum=json.mSum;
                        Toast.clear();
                        Toast.success("成功");
                    } else{
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail("数据获取异常！");
                    return;
                }
            });

            // var id=this.materialList.length+1;
            // var item={id:id,materialBarcode:'BZGJH',materialNumber:'123456',materialName:'托盘',materialBatch:'10000',stockName:'仓库',stockPlace:'仓位'};
            // this.materialList.push(item);
        },
        onBack(){
            android.back();
        },
        onSubmit(){
            if(this.materialList[0].mSum>this.sum){
                Toast.fail("拆包后数量不得超过初始数量！");
                return;
            }
            Toast.loading({
                duration:0,
                message: '数据提交中...',
                forbidClick: true
            });
            var mateBarcode=[];
            this.materialList.some((item, i)=>{
                mateBarcode.push(item.materialBarcode);
　　　　　　　});
            this.$api.post("/updateBarcode",{BarcodeNumber:this.barcodeNumber,mSum:this.materialList[0].mSum},response=>{
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        Toast.clear();
                        Toast.success(response.data.msg);
                        android.back();
                    } else{
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail("数据获取异常！");
                    return;
                }
            });
        },
        openScanner(){
            android.openScanner();
        },
        scannerSearch(data){
            this.barcodeNumber=data;
            this.dealBarcodeNumber();
        }
    }
}
</script>


