<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                ref="search"
                v-model="barcodeNumber"
                placeholder="请输入条码"
                show-action
                shape="round"
                clear
                @search="dealBarcodeNumber"
                style="margin-right:1rem;margin-left:0.1rem"
                right-icon
                @keyup.enter="dealBarcodeNumber"
                >
                <div slot="action" disabled="loading" @click="dealBarcodeNumber">搜索</div>
                <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix='icon' name="saomiao" /></div>
            </van-search>
        </div>
        <div class="form-item">
            <h2 class="form-header">基本信息</h2>
            <van-row>
                <van-col span="12">
                    <van-field
                        v-model="baseData.printMachine"
                        label="打印机名称："
                        disabled
                        readonly
                        label-align="left"
                        clickable
                        @click="showPrintMachinePicker=true"
                    />
                    <van-popup v-model="showPrintMachinePicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="printMachines"
                            @cancel="showPrintMachinePicker = false"
                            @confirm="onPrintMachineConfirm"/>
                    </van-popup> 
                </van-col>
                <van-col span="12">
                    <van-field
                        v-model="baseData.labelName"
                        label="条码模板："
                        disabled
                        label-align="left"
                        readonly
                        clickable
                        @click="showLabelNamePicker=false"
                    />
                    <van-popup v-model="showLabelNamePicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="labelNames"
                            @cancel="showLabelNamePicker = false"
                            @confirm="onLabelNameConfirm"/>
                    </van-popup> 
                </van-col>
            </van-row>
        </div>
        <div class="form-item">
            <h2 class="form-header">条码明细</h2>
            <van-collapse v-model="activeName" accordion>
                <van-collapse-item :title="item.materialBarcode" :name="item.id" v-for="item in materialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.mName"
                                label="物料名称："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.mNumber"
                                label="物料编码："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                disabled
                                v-model="item.mSum"
                                label="数量："
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.unitName"
                                label="单位："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.warehouse"
                                label="仓库："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.position"
                                label="仓位："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="24">
                            <van-field
                                v-model="item.batch"
                                label="批号："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6" align="center">
                            <van-button class="button" size="mini" type="danger" @click="deleteMaterial">删除</van-button>
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div>
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack">返回</van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit">装箱</van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            baseData:{
                printMachine:'',
                labelId:'4',
                labelName:'包装箱条码'
            },
            barcodeNumber:'',
            list: [],
            loading: false,
            finished: false,
            activeName:'',
            materialList:[],
            printMachines:[],
            labelNames:[],
            showPrintMachinePicker:false,
            showLabelNamePicker:false
        };
  },
  mounted(){
     window.scannerSearch=this.scannerSearch;
  },
  created(){
      this.showLoading("正在加载数据...");
      this.labelNames=[{text:'原料标签',id:'1'},{text:'产成品标签',id:'2'},{text:'小包装标签',id:'3'}]
      this.getPrintMachineList();
  },
  methods: {
        showLoading(data){
            Toast.loading({
                duration:0,
                message: data,
                forbidClick: true
            });
        },
        getPrintMachineList(){
            this.$api.get("/getPrintMachineList",{},response=>{
                Toast.clear();
                if (response.status >= 200 && response.status < 300) {
                    if("true"==response.data.success){
                        var machineList=JSON.parse(response.data.data);
                        machineList.some((item,i)=>{
                            this.printMachines.push({id:i,text:item.name});
                        });
                        Toast.success("打印机获取成功！");
                    }else{
                        Toast.fail("打印机获取失败，请联系管理员！");
                    }
                } else {
                    Toast.fail("打印机获取失败，请联系管理员！");
                }
            });
        },
        dealBarcodeNumber(){
            if(this.barcodeNumber.length==0){
                return;
            }
            var flag=false;
            this.materialList.some((item, i)=>{
　　　　　　    if(item.materialBarcode==this.barcodeNumber){
                    flag=true;
　　　　　　　　　    return;
　　　　　　　　　}
　　　　　　　});
            if(flag){
                Toast.fail("条码已扫描");
　　　　　　　　　return;
            }
            Toast.loading({
                duration:0,
                message: '获取数据...',
                forbidClick: true
            });
            this.$api.get("/searchBarcodeNumber",{BarcodeNumber:this.barcodeNumber},response=>{
                this.barcodeNumber='';
                this.focusNextInput();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        var json=JSON.parse(response.data.msg);
                        var len=this.materialList.length
                        json.some((item,i)=>{
                            this.$set(item,'id',len+i+1);
                            this.$set(item,'materialBarcode',item.barcodeNumber);
                            this.materialList.push(item);
                        });
                        Toast.clear();
                        Toast.success("成功");
                    } else{
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail("数据获取异常！");
                    return;
                }
            });

            // var id=this.materialList.length+1;
            // var item={id:id,materialBarcode:'BZGJH',materialNumber:'123456',materialName:'托盘',materialBatch:'10000',stockName:'仓库',stockPlace:'仓位'};
            // this.materialList.push(item);
        },
        onBack(){
            android.back();
        },
        onSubmit(){
            Toast.loading({
                duration:0,
                message: '数据提交中...',
                forbidClick: true
            });
            if(this.baseData.printMachine.length==0||this.baseData.labelId==0||this.materialList.length==0){
                Toast.clear();
                Toast.fail("请选择打印机、打印模板及条码数据！");
                return;
            }
            var mateBarcode=[];
            this.materialList.some((item, i)=>{
                mateBarcode.push(item.materialBarcode);
　　　　　　　});
            this.$api.post("/createBoxBarcode",{mateBarcodeList:JSON.stringify(mateBarcode)},response=>{
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        var barcodeNumber=response.data.msg;
                        var barcodeNumbers=[];
                        barcodeNumbers.push(barcodeNumber);
                        console.log(barcodeNumber);
                        this.$api.post("/printBarcode",{jsonData:JSON.stringify(barcodeNumbers),printMachine:this.baseData.printMachine,labelName:this.baseData.labelId},response=>{
                            Toast.clear();
                            if (response.status >= 200 && response.status < 300) {
                                if("true"==response.data.success){
                                    Toast.success(response.data.msg);
                                    android.back();
                                }else{
                                    Toast.fail(response.data.msg);
                                }
                            } else {
                                Toast.fail("打印机连接失败！");
                            }
                        });
                    } else{
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail("数据获取异常！");
                    return; 
                }
            });
        },
        deleteMaterial(){
            this.materialList.some((item, i)=>{
　　　　　　    if(item.id==this.activeName){
　　　　　　　　    this.materialList.splice(i, 1)
　　　　　　　　　　return true
　　　　　　　　　}
　　　　　　　})
        },
        openScanner(){
            android.openScanner();
        },
        scannerSearch(data){
            this.barcodeNumber=data;
            this.dealBarcodeNumber();
        },
        onPrintMachineConfirm(data){
            this.baseData.printMachine=data.text;
            this.showPrintMachinePicker=false;
        },
        onLabelNameConfirm(data){
            this.baseData.labelName=data.text;
            this.baseData.labelId=data.id;
            this.showLabelNamePicker=false;
        },
        focusNextInput(){
            var inputs = document.getElementsByTagName("input");
            inputs[0].focus();
        }
    }
}
</script>


