<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                v-model="barcodeNumber"
                placeholder=""
                show-action
                shape="round"
                clear
                @search="onSearch"
                right-icon
                @keyup.enter="onSearch"
            >
            <div slot="action" disabled="loading" @click="onSearch">搜索</div>
            <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix='icon' name="saomiao" /></div>
        </van-search>
        </div>
        <div class="form-item">
            <h2 class="form-header">基本信息</h2>
            <van-row>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.dept"
                        label="领料部门："
                        disabled
                        label-align="left"
                        @click="showDeptsPicker=true"
                    />
                    <van-popup v-model="showDeptsPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="depts"
                            @cancel="showDeptsPicker = false"
                            @confirm="onDeptsConfirm"/>
                    </van-popup> 
                </van-col>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.customer"
                        label="客户："
                        disabled
                        label-align="left"
                        readonly
                        clickable
                        @click="showCustomersPicker=true"
                    />
                    <van-popup v-model="showCustomersPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="customers"
                            @cancel="showCustomersPicker = false"
                            @confirm="onCustomerConfirm"/>
                    </van-popup> 
                </van-col>
            </van-row>
            <van-row v-if="false"> 
                <van-col span="12">
                    <van-field
                        v-model="baseData.sourceType"
                        label="源单类型："
                        disabled
                        label-align="left"
                        @click="showSourceTypesPicker=true"
                    />
                    <van-popup v-model="showSourceTypesPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="sourceTypes"
                            @cancel="showSourceTypesPicker = false"
                            @confirm="onSourceTypeConfirm"/>
                    </van-popup> 
                </van-col>
                <van-col span="12">
                    <van-field
                        v-model="baseData.menuNumber"
                        label="选单号："
                        label-align="left"
                        disabled
                        @click="showMenuNumbersPicker=true"
                    />
                    <van-popup v-model="showMenuNumbersPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="menuNumbers"
                            @cancel="showMenuNumbersPicker = false"
                            @confirm="onMenuNumberConfirm"/>
                    </van-popup> 
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.requester"
                        label="领料人："
                        disabled
                        label-align="left"
                        @click="showRequestersPicker=true"
                    />
                    <van-popup v-model="showRequestersPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="requesters"
                            @cancel="showRequestersPicker = false"
                            @confirm="onRequesterConfirm"/>
                    </van-popup> 
                </van-col>
                <van-col span="12">
                    <van-field
                        v-model="baseData.consignor"
                        required
                        label="发货人："
                        label-align="left"
                        disabled
                        @click="showConsignorsPicker=true"
                    />
                    <van-popup v-model="showConsignorsPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="consignors"
                            @cancel="showConsignorsPicker = false"
                            @confirm="onConsignorConfirm"/>
                    </van-popup> 
                </van-col>
            </van-row>
            <van-row>
                <van-col span="24">
                    <van-field
                        v-model="baseData.purpose"
                        label="领料用途："
                        label-align="left"
                        autosize
                        rows="1"
                        type="textarea"
                    />
                </van-col>
            </van-row>
        </div>
        
        <div class="form-item">
            <h2 class="form-header">物料明细</h2>
            <van-collapse v-model="activeName" accordion>
                <van-collapse-item :title="item.mName" :name="item.id" v-for="item in materialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.mNumber"
                                label="物料编码："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.batch"
                                label="批号："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                required
                                v-model="item.warehouse"
                                label="仓库："
                                disabled
                                label-align="left"
                             @click="showStocksPicker=true"
                            />
                            <van-popup v-model="showStocksPicker" position="bottom">
                                <van-picker
                                    show-toolbar
                                    :columns="stocks"
                                    @cancel="showStocksPicker = false"
                                    @confirm="onStocksConfirm"/>
                            </van-popup> 
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.position"
                                label="仓位："
                                disabled
                                label-align="left"
                             @click="showStockPlacesPicker=true"
                            />
                            <van-popup v-model="showStockPlacesPicker" position="bottom">
                                <van-picker
                                    show-toolbar
                                    :columns="stockPlaces"
                                    @cancel="showStockPlacesPicker = false"
                                    @confirm="onStockPlacesConfirm"/>
                            </van-popup> 
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                required
                                v-model="item.mSum"
                                label="申请数量："
                                label-align="left"
                                type="number"
                                @change="setTotalPrice(item)"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                required
                                v-model="item.price"
                                label="单价："
                                label-align="left"
                                type="number"
                                @change="setTotalPrice(item)"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="24">
                            <van-field
                                disabled
                                v-model="item.totalPrice"
                                label="金额："
                                label-align="left"
                                type="number"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6" align="center">
                            <van-button class="button" size="mini" type="danger" @click="deleteMaterial">删除</van-button>
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div>
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack">返回</van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit">提交</van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            barcodeNumber:'',
            baseData:{
                dept:'',
                deptId:'',
                customer:'',
                customerId:'',
                sourceType:'',
                sourceTypeId:'',
                menuNumber:'',
                requester:'',
                requesterId:'',
                consignor:'',
                consignorId:'',
                purpose:''
            },
            activeName:'',
            materialList:[],
            showDeptsPicker:false,
            depts:[],
            showCustomersPicker:false,
            customers:[],
            showSourceTypesPicker:false,
            sourceTypes:[],
            showMenuNumbersPicker:false,
            menuNumbers:[],
            showRequestersPicker:false,
            requesters:[],
            showConsignorsPicker:false,
            consignors:[],
            showStocksPicker:false,
            stocks:[],
            showStockPlacesPicker:false,
            stockPlaces:[],

        };
    },
    created(){
        this.searchAllInfo();
    },
    methods: {
        showLoading(data){
            Toast.loading({
                duration:0,
                message: data,
                forbidClick: true
            });
        },
        searchAllInfo(){
            // this.depts=[{id:1,text:'财务部'},{id:2,text:'物控部'}];
            // this.customers=[{id:1,text:'镇江'},{id:2,text:'常州'}];
            // this.sourceTypes=[{id:1,text:'领料出库'},{id:2,text:'其他出库'}];
            // this.requesters=[{id:1,text:'张三'},{id:2,text:'李四'}];
            // this.consignors=[{id:1,text:'王五'},{id:2,text:'赵六'}];
            this.showLoading('正在获取数据...');
            this.$api.get("/selectAllInfo",{},response=>{
                Toast.clear();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        var json=response.data.data;
                        this.depts=json.deptInfo;
                        this.customers=json.customerInfo;
                        this.requesters=json.empInfo;
                        this.consignors=json.empInfo;
                        this.stockPlaces=json.stockPlaceInfo;
                        this.stocks=json.stockInfo;
                        Toast.success("数据获取成功");
                    } else{
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.fail("数据获取异常");
                    return;
                }
            });
        },
        onSubmit() {
            if(this.baseData.deptId.length==0||this.baseData.customerId.length==0||this.baseData.requesterId.length==0||this.baseData.consignorId.length==0){
                Toast.fail('请填写必填项');
                return;
            }
            if(this.materialList.length==0){
                Toast.fail('请输入物料');
                return;
            }
            var flag=true;
            this.materialList.some((item,i)=>{
                if(!item.totalPrice||item.totalPrice<=0||item.warehouse.length==0){
                    Toast.fail('请确认物料信息');
                    flag=false;
                    return true;
                }
            });
            if(flag){
                this.$api.post("/submitMiscellaneousIssue",{baseData:JSON.stringify(this.baseData),materialList:JSON.stringify(this.materialList)},response=>{
                    Toast.clear();
                    if (response.status >= 200 && response.status < 300) {
                        if(response.data.success=="true"){
                            Toast.success("数据提交成功");
                            android.back();
                        } else{
                            Toast.fail(response.data.msg);
                            return;
                        }
                    } else {
                        Toast.fail("数据提交异常！");
                        return;
                    }
                });
            }
        },
        onBack(){
            android.back();
        },
        onDeptsConfirm(data){
            this.baseData.dept=data.text;
            this.baseData.deptId=data.id;
            this.showDeptsPicker=false;
        },
        onCustomerConfirm(data){
            this.baseData.customer=data.text;
            this.baseData.customerId=data.id;
            this.showCustomersPicker=false;
        },
        onSourceTypeConfirm(data){
            this.baseData.sourceType=data.text;
            this.baseData.sourceTypeId=data.id;
            this.showSourceTypesPicker=false;
            this.menuNumbers=[{id:1,text:'1231'},{id:2,text:'4123'}];
        },
        onMenuNumberConfirm(data){
            this.baseData.menuNumber=data.text;
            this.showMenuNumbersPicker=false;
        },
        onRequesterConfirm(data){
            this.baseData.requester=data.text;
            this.baseData.requesterId=data.id;
            this.showRequestersPicker=false;
        },
        onConsignorConfirm(data){
            this.baseData.consignor=data.text;
            this.baseData.consignorId=data.id;
            this.showConsignorsPicker=false;
        },
        onStocksConfirm(data){
            this.materialList.some((item,i)=>{
                if(this.activeName==item.id){
                    this.$set(item,'warehouse',data.text);
                    this.$set(item,'warehouseId',data.id);
                }
            });
            this.showStocksPicker=false;
        },
        onStockPlacesConfirm(){
            this.materialList.some((item,i)=>{
                if(this.activeName==item.id){
                    this.$set(item,'position',data.text);
                    this.$set(item,'positionId',data.id);
                }
            });
            this.showStockPlacesPicker=false;
        },
        deleteMaterial(){
            this.materialList.some((item, i)=>{
　　　　　　    if(item.id==this.activeName){
　　　　　　　　    this.materialList.splice(i, 1)
　　　　　　　　　　return true
　　　　　　　　　}
　　　　　　　})
        },
        onSearch(){
            this.showLoading('正在获取数据...');
            this.$api.get("/searchBarcodeInfo",{'BarcodeNumber':this.barcodeNumber},response=>{
                Toast.clear();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        var json=JSON.parse(response.data.msg);
                        this.$set(json,'id',this.materialList.length+1);
                        var flag=true;
                        this.materialList.some((item,i)=>{
                            if(json.barcodeNumber==item.barcodeNumber){
                                Toast.fail("当前条码已扫描");
                                flag=false;
                                return true;
                            }else if(json.mId==item.mId&&json.batch==item.batch&&json.warehouseId==item.warehouseId&&json.positionId==item.positionId){
                                this.$set(item,'mSum',parseInt(item.mSum)+parseInt(json.mSum));
                                flag=false;
                                return true;
                            }
                        });
                        if(flag){
                            this.materialList.push(json);
                            Toast.success("数据获取成功");
                        }
                    } else{
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.fail("数据获取异常");
                    return;
                }
            });
        },
        setTotalPrice(item){
            var total=parseInt(item.price)*parseInt(item.mSum);
            if(Number.isNaN(total)){
                total=0;
            }
            this.$set(item,"totalPrice",total);
        },
        openScanner(){
            android.openScanner();
        },
        scannerSearch(data){
            this.barcodeNumber=data;
            this.onSearch();
        }
    }

}
</script>



