<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                v-model="barcodeNumber"
                placeholder=""
                show-action
                shape="round"
                clear
                @search="onSearch"
                right-icon
                @keyup.enter="onSearch"
            >
            <div slot="action" disabled="loading" @click="onSearch">搜索</div>
            <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix='icon' name="saomiao" /></div>
        </van-search>
        </div>
        <div class="form-item">
            <h2 class="form-header">基本信息</h2>
            <van-row>
                <van-col span="12">
                    <van-field
                        v-model="baseData.type"
                        label="销售业务类型："
                        disabled
                        label-align="left"
                    />
                </van-col>
                <van-col span="12">
                    <van-field
                        v-model="baseData.salesWay"
                        label="销售方式："
                        disabled
                        label-align="left"
                        readonly
                        clickable
                        @click="showSalesWayPicker=false"
                    />
                    <van-popup v-model="showSalesWayPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="salesWays"
                            @cancel="showSalesWayPicker = false"
                            @confirm="onSalesWayConfirm"/>
                    </van-popup> 
                </van-col>
            </van-row>
            <van-row>
                <van-col span="24">
                    <van-field
                        v-model="baseData.purchasingUnit"
                        label="购货单位："
                        disabled
                        label-align="left"
                        label-width="2rem"
                    />
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field
                        v-model="baseData.dept"
                        label="部门："
                        disabled
                        label-align="left"
                        label-width="2rem"
                    />
                </van-col>
                <van-col span="12">
                    <van-field
                        v-model="baseData.director"
                        label="主管："
                        disabled
                        label-align="left"
                        label-width="2rem"
                    />
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field
                        v-model="baseData.salesMan"
                        label="业务员："
                        disabled
                        label-align="left"
                        label-width="2rem"
                    />
                </van-col>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.shipper"
                        label="发货："
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showShipperPicker=true"
                    />
                    <van-popup v-model="showShipperPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="shippers"
                            @cancel="showShipperPicker = false"
                            @confirm="onShipperConfirm"/>
                    </van-popup> 
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.safeKeeping"
                        label="保管："
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showSafekeepingPicker=true"
                    />
                    <van-popup v-model="showSafekeepingPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="Safekeepings"
                            @cancel="showSafekeepingPicker = false"
                            @confirm="onSafekeepingConfirm"/>
                    </van-popup> 
                </van-col>
            </van-row>
        </div>
        
        <div class="form-item">
            <h2 class="form-header">物料明细</h2>
            <van-collapse v-model="activeName" accordion>
                <van-collapse-item :title="item.materialName" :name="item.id" v-for="item in materialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.materialNumber"
                                label="物料编码："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.materialBatch"
                                label="批号："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.stockName"
                                label="仓库："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.stockPlace"
                                label="仓位："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                disabled
                                v-model="item.materialSum"
                                label="应发数量："
                                label-align="left"
                                type="number"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.materialUnit"
                                label="单位："
                                label-align="left"
                                disabled
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                required
                                v-model="item.materialActualSum"
                                label="实发数量："
                                label-align="left"
                                type="number"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.materialUnit"
                                label="单位："
                                label-align="left"
                                disabled
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6" align="center">
                            <van-button class="button" size="mini" type="danger" @click="deleteMaterial">删除</van-button>
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div>
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack">返回</van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit">提交</van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            barcodeNumber:'',
            baseData:{
                // type:'',
                // typeId:'',
                // salesWay:'',
                // salesWayId:'',
                // purpose:'',
                // dept:'',
                // deptId:'',
            },
            activeName:'',
            showSalesWayPicker:false,
            salesWays:[{id:1,text:133},{id:2,text:13}],
            showShipperPicker:false,
            shippers:[{id:1,text:'张三'},{id:2,text:'李四'}],
            showSafekeepingPicker:false,
            Safekeepings:[],
            materialList:[]
        };
    },
    created(){
        this.showLoading('正在加载...');
        var data =this.$route.query.data;
        this.baseData.type=data[0].type;
        this.baseData.typeId=data[0].typeId;
        this.baseData.salesWay=data[0].salesWay;
        this.baseData.salesWayId=data[0].salesWayId;
        this.baseData.purchasingUnit=data[0].purchasingUnit;
        this.baseData.purchasingUnitId=data[0].purchasingUnitId;
        this.baseData.dept=data[0].dept;
        this.baseData.deptId=data[0].deptId;
        this.baseData.director=data[0].director;
        this.baseData.directorId=data[0].directorId;
        this.baseData.salesMan=data[0].salesMan;
        this.baseData.salesManId=data[0].salesManId;
        this.baseData.shipper=data[0].shipper;
        this.baseData.shipperId=data[0].shipperId;
        data.some((item,i)=>{
            item.materialList.some((mate,j)=>{
                this.materialList.push(mate);
            });
        });
        this.$api.get("/selectAllEmpInfo",{},response=>{
            Toast.clear();
            if (response.status >= 200 && response.status < 300) {
                if(response.data.success=="true"){
                    this.shippers=response.data.data;
                    this.Safekeepings=response.data.data;
                    Toast.success("数据获取成功");
                } else{
                    Toast.fail(response.data.msg);
                    return;
                }
            } else {
                Toast.fail("数据获取异常！");
                return;
            }
        });
    },
    mounted(){
        window.scannerSearch=this.scannerSearch;
    },
    methods: {
        showLoading(data){
            Toast.loading({
                duration:0,
                message: data,
                forbidClick: true
            });
        },
        onSubmit() {
            this.showLoading('正在提交数据...');
            this.$api.post("/submitSaleIssue",{baseData:JSON.stringify(this.baseData),materialList:JSON.stringify(this.materialList)},response=>{
                Toast.clear();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        Toast.success("数据提交成功");
                        android.back();
                    } else{
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.fail("数据提交异常！");
                    return;
                }
            });
        },
        onBack(){
            this.$router.go(-1);
        },
        onSalesWayConfirm(data){
            this.baseData.salesWay=data.text;
            this.baseData.salesWayId=data.id;
            this.showSalesWayPicker=false;
        },
        onShipperConfirm(data){
            this.baseData.shipper=data.text;
            this.baseData.shipperId=data.id;
            this.showShipperPicker=false;
        },
        onSafekeepingConfirm(data){
            this.baseData.safeKeeping=data.text;
            this.baseData.safeKeepingId=data.id;
            this.showSafekeepingPicker=false;
        },
        deleteMaterial(){
            this.materialList.some((item, i)=>{
　　　　　　    if(item.id==this.activeName){
　　　　　　　　    this.materialList.splice(i, 1)
　　　　　　　　　　return true
　　　　　　　　　}
　　　　　　　})
        },
        onSearch(){
            if(this.barcodeNumber.length==0){
                return;
            }
            this.$api.get("/searchBarcodeNumber",{BarcodeNumber:this.barcodeNumber},response=>{
                this.barcodeNumber='';
                this.focusNextInput();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        Toast.success("条码查询成功");
                        var materialInfoList=JSON.parse(response.data.msg);
                        this.materialList.some((item,i)=>{
                            if(item.stockPlace=="*"){
                                item.stockPlace="";
                            }
                            materialInfoList.some((materialInfo,i)=>{
                                if(item.materialNumber==materialInfo.mNumber&&item.materialBatch==materialInfo.batch&&item.stockName==materialInfo.warehouse&&item.stockPlace==materialInfo.position){
                                    item.materialActualSum=Number(item.materialActualSum)+Number(materialInfo.mSum);
                                }
                            });
                        });
                    } else {
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail("条码查询异常！");
                    return;
                }
            });
        },
        openScanner(){
            android.openScanner();
        },
        scannerSearch(data){
            this.barcodeNumber=data;
            this.onSearch();
        },
        focusNextInput(){
            var inputs = document.getElementsByTagName("input");
            inputs[0].focus();
        }
    }

}
</script>



