<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                v-model="barcodeNumber"
                placeholder=""
                show-action
                shape="round"
                clear
                @search="onSearch"
                right-icon
                @keyup.enter="onSearch"
            >
                <div slot="action" disabled="loading" @click="onSearch">搜索</div>
                <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix='icon' name="saomiao" /></div>
            </van-search>
        </div>
        <div class="form-item">
            <h2 class="form-header">基本信息</h2>
            <van-row>
                <van-col span="24">
                    <van-field
                        v-model="baseData.auxCheckNo"
                        label="盘点单号："
                        disabled
                        label-align="left"
                    />
                </van-col>
            </van-row>
        </div>
        
        <div class="form-item">
            <h2 class="form-header">物料明细</h2>
            <van-collapse v-model="activeName" accordion>
                <van-collapse-item :title="item.materialName" :name="item.id" v-for="item in materialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.materialCode"
                                label="物料编码："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.batchNumber"
                                label="批号："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.stockName"
                                label="仓库："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.stockPlaceName"
                                label="仓位："
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                disabled
                                v-model="item.qtyAct"
                                label="账存数量："
                                label-align="left"
                                type="number"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.unitName"
                                label="单位："
                                label-align="left"
                                disabled
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                required
                                v-model="item.auxCheckQty"
                                label="盘点数量："
                                label-align="left"
                                type="number"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.unitName"
                                label="单位："
                                label-align="left"
                                disabled
                            />
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div class="form-item">
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack">返回</van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit">提交</van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
import { Dialog } from 'vant';
export default {
    data() {
        return {
            barcodeNumber:'',
            baseData:{
                auxCheckNo:''
            },
            activeName:'',
            materialList:[],
            notInMaterialList:[]
        };
    },
    created(){
        this.baseData.auxCheckNo=this.$route.query.number;

        this.searchAuxCheckInfo();
    },
    methods: {
        showLoading(data){
            Toast.loading({
                duration:0,
                message: data,
                forbidClick: true
            });
        },
        searchAuxCheckInfo(){
            this.showLoading('正在获取数据...');
            this.$api.get("/auxCheck/getAuxCheckData",{auxCheckNo:this.$route.query.number},response=>{
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        this.materialList=response.data.data;
                        Toast.clear();
                    } else{
                        Toast.fail(response.data.msg);
                        Toast.clear();
                        return;
                    }
                } else {
                    Toast.fail("数据获取异常！");
                    Toast.clear();
                    return;
                }
            });
        },
        onSubmit() {
            if(this.notInMaterialList.length>0){
                var message="请将以下物料新增至盘点报告：\n";
                this.notInMaterialList.some((item,i)=>{
                   message=message+"物料："+item.materialName+"-"+item.batchNumber+"-"+item.stockName+"-"+
                        item.stockPlaceName+"-"+"盘点数量："+item.auxCheckQty+"\n"
                });
                Dialog.confirm({
                    title: '提醒',
                    message: message
                }).then(() => {
                    this.submit();
                });
            }else{
                this.submit();
            }
        },
        submit(){
            Toast.loading({
                duration:0,
                message: '正在提交数据...',
                forbidClick: true
            });
            this.$api.post("/auxCheck/updateAuxCheckData",{auxCheckNo:this.baseData.auxCheckNo,notInMaterialList:JSON.stringify(this.notInMaterialList),materialList:JSON.stringify(this.materialList)},response=>{
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        Toast.success(response.data.msg);
                        android.back();
                    } else{
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail("数据提交异常！");
                    return;
                }
            });
        },
        onBack(){
            this.$router.go(-1);
        },
        onSearch(){
            this.$api.get("/searchAuxBarcodeNumber",{BarcodeNumber:this.barcodeNumber},response=>{
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        Toast.success("条码查询成功");
                        var materialInfo=response.data.msg;
                        var flag1=true;
                        this.materialList.some((item,i)=>{
                            if(item.materialID==materialInfo.materialID&&item.batchNumber==materialInfo.batchNumber&&item.stockID==materialInfo.stockID&&item.stockPlaceID==materialInfo.stockPlaceID){
                                item.auxCheckQty=Number(item.auxCheckQty)+Number(materialInfo.auxCheckQty);
                                flag1=false;
                                return;
                            }
                        });
                        if(flag1){
                            var flag2=true;
                            this.notInMaterialList.some((item,i)=>{
                                if(item.materialID==materialInfo.materialID&&item.batchNumber==materialInfo.batchNumber&&item.stockID==materialInfo.stockID&&item.stockPlaceID==materialInfo.stockPlaceID){
                                    item.auxCheckQty=Number(item.auxCheckQty)+Number(materialInfo.auxCheckQty);
                                    flag2=false;
                                    return;
                                }
                            });
                            if(flag2){
                                this.notInMaterialList.push(materialInfo);
                            }
                        }
                    } else {
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail("条码查询异常！");
                    return;
                }
            });
        },
        openScanner(){
            android.openScanner();
        },
        scannerSearch(data){
            this.barcodeNumber=data;
            this.onSearch();
        }
    }

}
</script>



